.catalog {
  padding-top: 149px;
  padding-bottom: 147px;
  padding-left: 60px;
  padding-right: 60px;
  // outline: 1px solid red;
  overflow: hidden; // иногда кольца создают ненужную ширину
  background-color: $catalog-back;

  @media (max-width: $tablet-width-max) {
    padding-top: 48px;
    padding-left: 89px;
    padding-right: 89px;
    padding-bottom: 69.5px;
  }

  @media (max-width: $mobile-width-max) {
    padding-top: 50px;
    padding-bottom: 53px;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.catalog__title {
  max-width: 50%;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;

  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  color: $black-three;
  text-align: center;

  @media (max-width: $tablet-width-max) {
    margin-bottom: 30px;
    max-width: 100%;

    font-size: 24px;
    line-height: 42px;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 29px;
    // letter-spacing: ;
  }
}

.catalog__list {
  display: grid;
  max-width: 1126px;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 50px;
  margin: 0 auto;
  padding: 0;
  list-style: none;

  @media (max-width: $tablet-width-max) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (max-width: $mobile-width-max) {
    flex-direction: column;
    gap: 42px;
  }
}

.catalog__card {
  @media (max-width: $tablet-width-max) {
    width: calc(50% - 25px);
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
  }
}
